import React from 'react';

class Canvas extends React.Component
{
    fill    = "#C0C0C0";
    is_fill = true;

    constructor(props)
    {
        super(props)
        this.myRef = React.createRef();
    }

    get self()
    {
        return this.myRef.current;
    }

    get ctx()
    {
        return this.self.getContext("2d");
    }

    get canvas()
    {
        return this.ctx.canvas;
    }

    resize()
    {
        this.canvas.width  = this.self.parentElement.offsetWidth;
        this.canvas.height = this.self.parentElement.offsetHeight;
    }

    init()
    {
        this.resize();
        this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
        this.ctx.fillStyle = this.fill;

        if (this.is_fill)
        {
            this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
        }
    }

    componentDidMount()
    {
        this.init();
    }

    render()
    {
        return (<canvas ref={this.myRef} {...this.props}></canvas>);
    }
}

export default Canvas;
