import Canvas from "./canvas.js";

class Circle extends Canvas
{
    constructor(props)
    {
        super(props);
        this.is_fill = false;
    }

    get radius()
    {
        return Math.min(this.canvas.width, this.canvas.height) * 0.5;
    }

    componentDidMount()
    {
        this.init();
        this.ctx.beginPath();
        this.ctx.arc(this.radius, this.radius, this.radius, 0, 2 * Math.PI);
        this.ctx.fill();
    }
}

export default Circle;
