import "../styles/timeline.css";

import React        from "react";
import EosBrief     from "./briefs/eos_brief.js";
import Circle       from "./templates/circle.js";
import VerticalLine from "./templates/vertical_line.js";

class Timeline extends React.Component
{
    id = "Timeline";

    render()
    {
        return (
            <div className="Timeline">
                <div style={{width:"100%", display:"flex", flexDirection:"row", margin:"auto"}}>
                    <div style={{display:"flex", flexDirection:"column", flex:"0.1"}}>
                        <div style={{width:"25px", height:"25px", margin:"auto"}}><Circle      ></Circle      ></div>
                        <div style={{width:"25px", height:"100%", margin:"auto"}}><VerticalLine></VerticalLine></div>
                    </div>
                    <div className="Slot Project-Left"><EosBrief/></div>
                </div>
            </div>
        );
    }
}

export default Timeline;
