import Canvas from "./canvas.js";

class VerticalLine extends Canvas
{
    thickness = 10;

    constructor(props)
    {
        super(props);
        this.is_fill = false;
    }

    componentDidMount()
    {
        this.init();
        const x = Math.round((this.canvas.width - this.thickness) * 0.5);
        this.ctx.fillRect(x, 0, this.thickness, this.canvas.height);
    }
}

export default VerticalLine;
