import "../styles/profile.css";

import React        from "react";
import profile_bg   from "../media/profile_bg.png";
import profile_pic  from "../media/profile.png";
import github       from "../media/icons/github-icon.svg";
import gitlab       from "../media/icons/gitlab-icon.svg";
import vs2019       from "../media/icons/vs2019-icon.svg";
import vscode       from "../media/icons/vscode-icon.svg";
import opengl       from "../media/icons/opengl-icon.svg";
import qt           from "../media/icons/qt-icon.svg";
import react        from "../media/icons/react-icon.svg";
import ue4          from "../media/icons/unreal_engine-icon.svg";
import unity        from "../media/icons/unity-icon.svg";
import photoshop    from "../media/icons/photoshop-icon.svg";
import illustrator  from "../media/icons/illustrator-icon.svg";
import premiere_pro from "../media/icons/premiere_pro-icon.svg";
import mixcraft_8   from "../media/icons/mixcraft_8-icon.png";

class Profile extends React.Component
{
    constructor(props)
    {
        super(props);
        this.main = React.createRef();
    }

    OnResize = () =>
    {
        const image = new Image();
        image.src = profile_bg;
        const height = (this.main.current.offsetWidth / image.width) * image.height;
        this.main.current.style.minHeight = height + "px";
        console.log("resized");
    }

    componentDidMount()
    {
        this.OnResize();
    }

    componentWillUnmount()
    {
        window.removeEventListener("resize", this.OnResize);
    }

    render()
    {
        window.addEventListener("resize", this.OnResize);

        return (
            <div className="Profile" ref={this.main}>
                <div className="Profile-Margin"></div>
                <div className="Profile-Padding"></div>
                <div className="Profile-Block">
                    <img className="Profile-Img" ref={this.profile_pic} src={profile_pic} alt="Profile Pic"></img>
                    <div className="Profile-Content">
                        <p>
                            I'm a code junky whose strong suit comes in tools programming. I love developing things
                            that people can use and benefit from. This extends to design where I love to deliver
                            my content to people in both an aesthetically pleasing yet practical manner.
                        </p>

                        <p>
                            Absolutely love narrative-driven content and discussing with others on its presentation
                            and message. I draw from time to time as a hobby and love listening to music - no specific
                            genre, just music that makes my heart sing ♪
                        </p>

                        <div style={{display:"flex", flexDirection:"row"}}>
                            <div style={{flex:"1"}}>
                                <h3>Programming</h3>

                                <img className="Icon" src={github} alt="Github"            ></img>
                                <img className="Icon" src={gitlab} alt="Gitlab"            ></img>
                                <img className="Icon" src={vs2019} alt="Visual Studio 2019"></img>
                                <img className="Icon" src={vscode} alt="Visual Studio Code"></img>
                                <img className="Icon" src={opengl} alt="OpenGL"            ></img>
                                <img className="Icon" src={qt    } alt="Qt5"               ></img>
                                <img className="Icon" src={react } alt="react-app"         ></img>
                            </div>

                            <div style={{flex:"1"}}>
                                <h3>Design</h3>

                                <img className="Icon" src={ue4         } alt="Unreal Engine 4"  ></img>
                                <img className="Icon" src={unity       } alt="Unity"            ></img>
                                <img className="Icon" src={photoshop   } alt="Adobe Photoshop"  ></img>
                                <img className="Icon" src={illustrator } alt="Adobe Illustrator"></img>
                                <img className="Icon" src={premiere_pro} alt="Adobe Premier Pro"></img>
                                <img className="Icon" src={mixcraft_8  } alt="Mixcraft 8"       ></img>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="Profile-Margin"></div>
            </div>
        );
    }
}

export default Profile;
